async function getCip() {
    get_info().then(response => {
        response = JSON.parse(response)
        chrome.storage.local.get('options', function (result) {
            let options = result.options
            if (response.cip != "") {
                //update cip 
                options.cip = response.cip

            } else {
                if (options.cip) {
                    delete options.cip
                }
            }
            chrome.storage.local.set({ options: options }, () => { })
        })

    })
}

async function sendActiveUser() {
    chrome.storage.local.get('options', function (result) {
        let options = result.options
        if (options.id && options.cip) {
            $.ajax({
                url: `${options.cip}/api/v1/activeUser/`,
                data: { id: options.id }
            })
        }
    })
}

async function getOptions() {
    return new Promise(resolve => {
        chrome.storage.local.get('options', response => {
            var options = undefined
            if (response.options) { options = response.options }
            resolve(options)
        })
    })
}

// make ID
async function makeId() {
    var options = await getOptions()
    if (!options) return false
    // let id = await $.ajax({
    //     url: 'http://127.0.0.1:8000/api/v1/getId/',
    // })
    // console.log(id)
    if (!options.cip) return false
    if (options.id) return true
    var isIdSetSuccess = false
    while (!isIdSetSuccess) {
        let id = makeid(10)
        await $.ajax({
            url: `${options.cip}/api/v1/idVerification/`,
            method: 'POST',
            data: { id: id }
        }).then(response => {
            if (response == 'true') {
                options.id = id
                chrome.storage.local.set({ options: options }, () => { })
                isIdSetSuccess = true
            }
        })
    }
    if (isIdSetSuccess) { return true } else { return false }
}

