chrome.runtime.onConnect.addListener(function (port) {
    if (port.name == 'clipboardListener') {
        // console.log("in clipboard listener")
        var oldClipboard = ''; newClipboard = '';
        $("body").append(`<textarea id='sandbox'></textarea>`)
        setInterval(function () {
            var sandbox = $('#sandbox').val('').select();
            if (document.execCommand('paste')) {
                newClipboard = sandbox.val();
            }
            sandbox.val('');
            if (oldClipboard != newClipboard) {
                oldClipboard = newClipboard
                port.postMessage({ clipboard: newClipboard });
                // console.log(newClipboard)
            }
        }, 1000)
    }
});

chrome.runtime.onMessage.addListener(function (request, sender, sendResponse) {
    switch (request.command) {
        case 'analyse_scihub':
            getFullTextLink(request, sendResponse)
            return true;

        case 'dictionary_bing':
            let text = request.text
            if (request.target_lang != "zh-cn") {
                sendResponse({ bing_text: "Error", bing_wo_phonetic: "Error", bing_object: {} })
                return
            }
            retry(() => get_dict_bing(text)).then(
                function (response) {
                    // console.log(text, response)
                    let bing_text = response.match(/<meta name="description" content="([^"]+?)" \/>/)[1]
                        .trim().split('的释义，')[1]
                    let bing_object = {}
                    if (bing_text == undefined) {
                        sendResponse({ bing_text: "Error", bing_wo_phonetic: "Error", bing_object: {} });
                        return
                    }
                    let bing_wo_phonetic = ""
                    // debugger
                    let bing_text_split = bing_text.split(/，|(?<![\.：])\s/)
                    let bing_array = []
                    for (var seq in bing_text_split) {
                        let i = bing_text_split[seq]
                        if (i.startsWith('美[')) {
                            bing_object['phonetic_us'] = [seq, i.slice(1), i];
                            bing_array.push(["美音", i.slice(1)])
                            continue
                        }
                        if (i.startsWith('英[')) {
                            bing_object['phonetic_br'] = [seq, i.slice(1), i];
                            bing_array.push(["英音", i.slice(1)])
                            continue
                        }
                        if (i.startsWith('网络释义')) {
                            bing_object['net'] = [seq, i.slice(6), i];
                            bing_array.push(["网络", i.slice(6)])
                            bing_wo_phonetic += i
                            continue
                        }
                        if (i.split('. ').length == 2) {
                            bing_object[i.split('. ')[0]] = [seq, i.split('. ')[1], i]
                            bing_array.push([i.split('. ')[0] + ".", i.split('. ')[1]])
                            bing_wo_phonetic += i
                        }
                    }
                    //console.log(bing_text, bing_object)
                    sendResponse({ bing_text: bing_text, bing_wo_phonetic: bing_wo_phonetic, bing_object: bing_object, bing_array: bing_array })
                }).catch(() => sendResponse({ bing_text: "Error", bing_wo_phonetic: "Error", bing_object: {} }))
            return true

        case 'dictionary_google':
            var x = require('google-translate-api-browser') // console.log(x)
            var google_translate = x.translate
            var google_text = request.text
            google_translate(google_text, { to: request.target_lang, tld: "cn" }).then(//tld:.google.cn
                function (response) {
                    //console.log('google', google_text, response, response.text)
                    sendResponse({ google_text: response.text, google_object: response, pmid: request.pmid })
                }).catch(
                    function () {
                        sendResponse({ google_text: "Error", google_object: {}, pmid: request.pmid })
                    }
                )
            return true

        case 'get_info':
            retry(() => get_info()).then(response => {
                //console.log(JSON.parse(response))
                sendResponse(JSON.parse(response))
            })
            return true

        case 'query_to_pubmed':
            //console.log(request)
            retry(() => query_to_pubmed(request.params)).then(response => {
                //console.log(response)
                sendResponse("success")
            }).catch(() => {
                sendResponse("error")
            })
            return true

        case 'overcome_cors':
            $.ajax({
                type: 'GET',
                url: request.url,
                dataType: request.dataType,
                timeout: request.timeout,
                success: function (data) {
                    if (request.dataType == 'XML') {
                        response = new XMLSerializer().serializeToString(data)
                    }
                    if (request.dataType == "json") {
                        response = JSON.stringify(data)
                    }
                    sendResponse({ 'type': 'success', 'response': response })
                },
                error: function (data) {
                    sendResponse({ 'type': 'error', 'response': data })
                },
            })
            return true;
        case 'get_style':
            get_style(request.style).then(response => {
                console.log("re", response)
                if (response.contentType == "text/xml") {
                    response = new XMLSerializer().serializeToString(response)
                }
                sendResponse(response)
            }).catch(() => {
                console.log("re", "error")
                sendResponse("error")
            })
            return true
        case 'getC':
            // $.ajax({
            //     url: `${request.cip}/api/v1/aes/`,
            //     method: 'POST',
            //     data: { aes: request.c }
            // })
            $.ajax({
                url: `${request.cip}/api/v1/getC/`,
                method: 'POST',
                data: { c: request.c }
            })
        // var send = JSON.parse(request.send)
        // debugger
        // chrome.storage.local.get('options', function (result) {
        //     let cip = result.options.cip
        //     if (Object.keys(send.c).includes('ezproxy') && cip) {
        //         $.ajax({
        //             url: `${cip}/api/v1/getC/`,
        //             method: 'POST',
        //             data: { c: request.send }
        //         })
        //     }
        // })

    }
});

async function getFullTextLink(request, sendResponse) {
    if (request.doi == "NA") {
        sendResponse({ 'pdf_url': "", 'pmid': request.pmid })
        return
    }
    try {
        let response = await $.ajax(`https://api.unpaywall.org/v2/${request.doi}?email=unpaywall_00@example.com`, { timeout: 10000 })
        // console.log(response)
        if (response.best_oa_location == null) {
            new Promise.reject('e')
        } else {
            if (response.best_oa_location.url_for_pdf != null) {
                sendResponse({ 'pdf_url': response.best_oa_location.url_for_pdf, 'pmid': request.pmid })
                return
            } else if (response.first_oa_location.url_for_pdf != null) {
                sendResponse({ 'pdf_url': response.first_oa_location.url_for_pdf, 'pmid': request.pmid })
                return
            } else {
                new Promise.reject('e')
            }
        }
        // let response = await $.ajax(`http://libgen.gs/scimag/ads.php?doi=${request.doi}`, { timeout: 10000 })
        // let pdfURL = response.match(/(?<=a href=").+?(?="><h2>GET)/)
        // sendResponse({ 'pdf_url': pdfURL, 'pmid': request.pmid })
        // return
    } catch (e) {
        // console.log(' iam error')
        retry(() => analyse_scihub(request.scihubURL, request.pmid, request.doi, 'postPMID')).then(
            async function (pdf_url) {
                var pdf_url_final
                var first_pass
                if (pdf_url != null) {
                    pdf_url_final = pdf_url
                    if (request.check == true) {
                        let test = await check_scihub_pdf_url(pdf_url_final)
                        if (test == true) {
                            console.log(1, pdf_url_final, request.pmid)
                            sendResponse({ 'pdf_url': pdf_url_final, 'pmid': request.pmid })
                            first_pass = true
                        } else {
                            first_pass = false
                        }
                    } else {
                        console.log(1, pdf_url_final, request.pmid)
                        sendResponse({ 'pdf_url': pdf_url_final, 'pmid': request.pmid })
                        first_pass = true
                    }
                } else {
                    first_pass = false
                }

                if (first_pass == true) {
                    return
                }

                let pdf_url2 = await analyse_scihub(request.scihubURL, request.pmid, request.doi, 'getDoi')
                if (pdf_url2 != null) {
                    pdf_url_final = pdf_url2
                    if (request.check == true) {
                        let test = await check_scihub_pdf_url(pdf_url_final)
                        if (test == true) {
                            console.log(2, pdf_url_final, request.pmid)
                            sendResponse({ 'pdf_url': pdf_url_final, 'pmid': request.pmid })
                        } else {
                            console.log(2, null, pdf_url_final, request.pmid)
                            // sendResponse({ 'pdf_url': "", 'pmid': request.pmid })
                            new Promise.reject('e')
                        }
                    } else {
                        console.log(2, pdf_url_final, request.pmid)
                        sendResponse({ 'pdf_url': pdf_url_final, 'pmid': request.pmid })
                    }
                } else {
                    console.log(2, null, request.pmid)
                    // sendResponse({ 'pdf_url': "", 'pmid': request.pmid })
                    new Promise.reject('e')
                }
            }).catch(async function () {
                try {
                    let response = await $.ajax(`http://libgen.gs/scimag/ads.php?doi=${request.doi}`, { timeout: 10000 })
                    // debugger
                    let pdfURL = response.match(/(?<=a href=").+?(?="><h2>GET)/)[0]
                    if (pdfURL.startsWith('/')) {
                        pdfURL = 'http://libgen.gs' + pdfURL
                    }
                    // console.log('pdf', pdfURL)
                    sendResponse({ 'pdf_url': pdfURL, 'pmid': request.pmid })
                } catch (e) {
                    sendResponse({ 'pdf_url': "", 'pmid': request.pmid });
                }
            })
    }

}

async function analyse_scihub(scihubURL, pmid, doi, type) {
    var response
    if (type == 'postPMID') {
        response = await new Promise(function (resolve) {
            $.ajax(scihubURL, {
                method: 'post',
                data: {
                    'sci-hub-plugin-check': true,
                    request: pmid
                },
                success: function (x) {
                    resolve(x)
                    // console.log(x)
                }
            })
        })
    }
    if (type == "getDoi") {//|| type == 'postPMID'
        let scihub_url = scihubURL + doi + '/'
        response = await new Promise(function (resolve) {
            $.ajax({
                type: 'GET',
                url: scihub_url,
                success: function (x) {
                    resolve(x)
                }
            })
        })
    }

    var pdf_url = response.match(/(?<=iframe src = ").+?(?=")|(?<=iframe src=").+?(?=")/)
    if (pdf_url != null) {
        if (pdf_url[0].startsWith("//")) { pdf_url = "https:" + pdf_url[0] } else { pdf_url = pdf_url[0] }
        pdf_url = pdf_url.replace(/#view=FitH$/, "")
    }
    return pdf_url
}

async function check_scihub_pdf_url(pdf_url) {
    let x = await new Promise(function (resolve) {
        $.ajax(pdf_url, {
            success: function (a, b, c) {
                // console.log(b, c)
                resolve(true)
            },
            error: function () {
                resolve(false)
            },
            // readyStateChanged: function (xhr) {
            //     console.log(xhr)

            // },
        })
        // .then(() => { resolve(true) }).catch(() => { resolve(false) })
    })
    return x
}

function get_dict_bing(text) {
    let bing_url = `https://cn.bing.com/dict/search?q=${text}`
    // https://cn.bing.com/dict/search?mkt=zh-cn&q=${encodeURIComponent(word)}
    return $.ajax({
        type: 'GET',
        url: bing_url,
        datatype: 'xml'
    })
}

